<template>
  <div class="checkout-page">
    <div class="checkout-header">
      <h1>Checkout</h1>
      <div class="steps">
        <div class="step active">
          <span class="step-number">1</span>
          <span class="step-text">Shipping</span>
        </div>
        <div class="step" :class="{ active: isPaymentStep }">
          <span class="step-number">2</span>
          <span class="step-text">Payment</span>
        </div>
      </div>
    </div>

    <div class="checkout-content">
      <div class="checkout-form">
        <form @submit.prevent="handleSubmit">
          <h2>Shipping Information</h2>
          
          <div class="form-grid">
            <div class="form-group">
              <label>First Name</label>
              <input 
                v-model="form.firstName"
                type="text"
                required
                :class="{ 'error': errors.firstName }"
              >
              <span class="error-message" v-if="errors.firstName">
                {{ errors.firstName }}
              </span>
            </div>
            <div class="form-group">
              <label>Last Name</label>
              <input 
                v-model="form.lastName"
                type="text"
                required
                :class="{ 'error': errors.lastName }"
              >
              <span class="error-message" v-if="errors.lastName">
                {{ errors.lastName }}
              </span>
            </div>
          </div>

          <div class="form-group">
            <label>Email</label>
            <input 
              v-model="form.email"
              type="email"
              required
              :class="{ 'error': errors.email }"
            >
            <span class="error-message" v-if="errors.email">
              {{ errors.email }}
            </span>
          </div>

          <div class="form-group">
            <label>Street Address</label>
            <input 
              v-model="form.address"
              type="text"
              required
              :class="{ 'error': errors.address }"
            >
            <span class="error-message" v-if="errors.address">
              {{ errors.address }}
            </span>
          </div>

          <div class="form-grid">
            <div class="form-group">
              <label>Postal Code</label>
              <div class="postal-code-group">
                <input 
                  v-model="form.postalCode"
                  type="text"
                  required
                  @blur="lookupAddress"
                  :class="{ 'error': errors.postalCode }"
                >
                <span class="error-message" v-if="errors.postalCode">
                  {{ errors.postalCode }}
                </span>
              </div>
            </div>
            <div class="form-group">
              <label>City</label>
              <input 
                v-model="form.city"
                type="text"
                required
                :class="{ 'error': errors.city }"
              >
              <span class="error-message" v-if="errors.city">
                {{ errors.city }}
              </span>
            </div>
          </div>

          <button type="submit" class="submit-button">
            Continue to Payment
          </button>
        </form>
      </div>

      <div class="order-summary">
        <h2>Order Summary</h2>
        <div class="order-items">
          <div v-for="item in cart.state.items" 
               :key="item.id" 
               class="order-item">
            <div class="item-info">
              <img :src="item.image_url" :alt="item.name">
              <div>
                <h4>{{ item.name }}</h4>
                <p>Qty: {{ item.quantity }}</p>
              </div>
            </div>
            <div class="item-price">
              ${{ (item.price * item.quantity).toFixed(2) }}
            </div>
          </div>
        </div>
        
        <div class="summary-totals">
          <div class="summary-row">
            <span>Subtotal</span>
            <span>${{ cart.state.total.toFixed(2) }}</span>
          </div>
          <div class="summary-row">
            <span>Shipping</span>
            <span>Free</span>
          </div>
          <div class="summary-total">
            <span>Total</span>
            <span>${{ cart.state.total.toFixed(2) }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive, ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import cart from '@/store/cart'
import { supabase } from '@/services/supabase'

export default defineComponent({
  name: 'Checkout',
  setup() {
    const router = useRouter()
    const isPaymentStep = ref(false)

    // Use reactive instead of ref for form data
    const form = reactive({
      firstName: '',
      lastName: '',
      email: '',
      address: '',
      postalCode: '',
      city: '',
    })

    const errors = reactive({})

    // Simplified postal code validation for Dutch format
    const validatePostalCode = (postalCode) => {
      const regex = /^[1-9][0-9]{3}\s?[A-Za-z]{2}$/
      return regex.test(postalCode)
    }

    // Replace API lookup with simple format check
    const lookupAddress = () => {
      if (!validatePostalCode(form.postalCode)) {
        errors.postalCode = 'Please enter a valid postal code (e.g., 1234 AB)'
        return
      }
      // Format postal code to standard format (1234 AB)
      form.postalCode = form.postalCode
        .replace(/\s/g, '')
        .replace(/([0-9]{4})([A-Za-z]{2})/, '$1 $2')
        .toUpperCase()
    }

    // Fetch user's saved address if available
    const fetchUserAddress = async () => {
      try {
        const { data: { user } } = await supabase.auth.getUser()
        if (!user) return

        const { data, error } = await supabase
          .from('profiles')  // Changed from user_profiles to profiles
          .select('*')
          .eq('id', user.id)  // Changed from user_id to id
          .single()

        if (error) {
          // Only log error if it's not a "not found" error
          if (error.code !== 'PGRST116') {
            console.error('Error fetching profile:', error)
          }
          return
        }

        if (data) {
          form.firstName = data.first_name || ''
          form.lastName = data.last_name || ''
          form.email = user.email || ''
          form.address = data.address || ''
          form.postalCode = data.postal_code || ''
          form.city = data.city || ''
        }
      } catch (error) {
        console.error('Error fetching user address:', error)
      }
    }

    // Modified validateForm to use reactive form
    const validateForm = () => {
      Object.keys(errors).forEach(key => delete errors[key])

      if (!form.firstName) errors.firstName = 'First name is required'
      if (!form.lastName) errors.lastName = 'Last name is required'
      if (!form.email) errors.email = 'Email is required'
      if (!form.address) errors.address = 'Address is required'
      if (!form.postalCode) {
        errors.postalCode = 'Postal code is required'
      } else if (!validatePostalCode(form.postalCode)) {
        errors.postalCode = 'Please enter a valid postal code'
      }
      if (!form.city) errors.city = 'City is required'

      return Object.keys(errors).length === 0
    }

    // Modified handleSubmit to use reactive form
    const handleSubmit = async () => {
      if (!validateForm()) return

      try {
        const { data: { user } } = await supabase.auth.getUser()
        if (!user) throw new Error('No user found')

        // First update/create profile
        const { error: profileError } = await supabase
          .from('profiles')
          .upsert({
            id: user.id,  // Changed from user_id to id
            first_name: form.firstName,
            last_name: form.lastName,
            address: form.address,
            postal_code: form.postalCode,
            city: form.city
          })

        if (profileError) throw profileError

        // Then create order
        const { data: order, error: orderError } = await supabase
          .from('orders')
          .insert({
            user_id: user.id,
            status: 'pending',
            items: cart.state.items,
            total_amount: cart.state.total,
            payment_method: 'pending', // Add this line
            shipping_address: {
              first_name: form.firstName,
              last_name: form.lastName,
              email: form.email,
              address: form.address,
              postal_code: form.postalCode,
              city: form.city
            }
          })
          .select()
          .single()

        if (orderError) throw orderError

        // Clear cart and redirect
        cart.clear()
        router.push({
          name: 'OrderConfirmation',
          params: { orderId: order.id }
        })
      } catch (error) {
        console.error('Error processing order:', error)
        alert('There was an error processing your order. Please try again.')
      }
    }

    onMounted(() => {
      fetchUserAddress()
    })

    return {
      cart,
      form,
      errors,
      isPaymentStep,
      handleSubmit,
      lookupAddress
    }
  }
})
</script>

<style scoped>
.checkout-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: calc(100vh - 64px);
}

.checkout-header h1 {
  font-size: 40px;
  font-weight: 700;
  letter-spacing: -0.025em;
  background: linear-gradient(135deg, #ffffff 0%, #a7a7a7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.steps {
  display: flex;
  gap: 24px;
  margin-top: 24px;
}

.step {
  display: flex;
  align-items: center;
  gap: 12px;
  color: var(--text-secondary);
  font-size: 15px;
}

.step.active {
  color: var(--accent-primary);
}

.step-number {
  width: 28px;
  height: 28px;
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  font-size: 14px;
  font-weight: 600;
}

.step.active .step-number {
  background: var(--accent-primary);
  color: white;
}

.checkout-content {
  display: grid;
  grid-template-columns: 1fr 400px;
  gap: 32px;
  margin-top: 40px;
}

.checkout-form {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  padding: 32px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.form-group {
  margin-bottom: 24px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  color: var(--text-secondary);
  font-size: 14px;
  font-weight: 500;
}

.form-group input {
  width: 100%;
  padding: 16px;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
  font-size: 15px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.form-group input:focus {
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 4px rgba(var(--accent-primary-rgb), 0.15);
  outline: none;
}

.submit-button {
  width: 100%;
  padding: 16px;
  background: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  margin-top: 32px;
  transition: all 0.3s ease;
  background: linear-gradient(135deg, var(--accent-primary), var(--system-purple));
}

.submit-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(var(--accent-primary-rgb), 0.3);
}

.order-summary {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  padding: 32px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  position: sticky;
  top: 100px;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  color: var(--text-secondary);
}

.summary-total {
  border-top: 1px solid var(--card-border);
  margin-top: 12px;
  padding-top: 12px;
  font-weight: 600;
  font-size: 18px;
  display: flex;
  justify-content: space-between;
}

/* Error states */
.error {
  border-color: var(--system-red) !important;
}

.error-message {
  color: var(--system-red);
  font-size: 12px;
  margin-top: 4px;
}

.postal-code-group {
  position: relative;
}

/* Responsive Design */
@media (max-width: 768px) {
  .checkout-content {
    grid-template-columns: 1fr;
  }
  
  .order-summary {
    position: static;
  }
  
  .form-grid {
    grid-template-columns: 1fr;
  }
}
</style>
